.header {
    margin: 0px;
    padding: 15px 30px;
    min-height: 15vh;
    background-color: grey;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .header__logo{
    width: 25px;
    height: 25px;
  
    @media(min-width: 768px) {
      width: 40px;
      height: 40px;
    }
  }
  
  .title {
    padding-bottom: 10px;
    margin: 0px;
    width: max-content;
    font-size: 24px;
    font-weight: 500;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 35px;
    border-bottom: 4px solid black;

    @media(min-width: 768px) {
      height: 50px;
      font-size: 36px;
      font-weight: 500;
      }
  
  }